import {
  Card,
  Container,
  Divider,
  IconButton,
  ImageList,
  ImageListItem,
  ImageListItemBar,
  Stack,
} from '@mui/material';

import QRCode from 'qrcode.react';
import { useState } from 'react';
import { useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';

import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button'; // Import Button component from Material-UI

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

import Iconify from 'template/components/iconify';

// ၁. state management က အဖွဲ့တွေ
import { useSelector, useDispatch } from 'react-redux';
// react router
import { useNavigate } from 'react-router-dom';

import { useRouter } from 'template/routes/hooks';

// ၂. မဖြစ်မနေ သုံးကြမယ့်  Slice က function တွေ
import {
  // thunks
  // selectors
  selectSelectedShop,
  selectShopDeleteStatus,
  selectShopError,
  // actions
  setShopDeleteStatus,
  setSelectedCategory,
} from 'features/main/mainSlice';
import {
  // thunks
  deleteShopAsyncThunk,
} from 'features/shop/shopSlice';

import {
  // thunks
  // selectors
  selectAccessToken,
  // actions
} from 'features/user/userSlice';
import { Margin } from '@mui/icons-material';

/* အဆင့်ဆင့် pass ရတာ ငရဲခံနေရသလိုပဲမို့ sideEffect သုံးပြီး ပဲ data ယူတော့တာမျိုး */
export default function ShopDetailView() {
  // core
  const { id } = useParams();
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const router = useRouter();

  // internal state
  const [open, setOpen] = useState(false);

  // selector hooks
  const access_token = useSelector(selectAccessToken);

  // shop section
  const shop = useSelector(selectSelectedShop);
  const shop_delete_status = useSelector(selectShopDeleteStatus);
  const error = useSelector(selectShopError);
  // const shop = shopData.find((shop) => shop.id === parseInt(id, 10));

  const handleClose = (id, access_token) => {
    console.log('id is ' + id);
    console.log('access_token is ' + access_token);
    if (id !== 0) {
      // dispatch delete shop api
      let formData = new FormData();
      formData.append('access_token', access_token);
      formData.append('id', id);
      dispatch(deleteShopAsyncThunk(formData));
    }
    setOpen(false);
  };

  const handleErrorDialogClose = () => {
    dispatch(setShopDeleteStatus('idle'));
  };

  const handleCategoryCardClick = (category) => {
    // set category detail and
    dispatch(setSelectedCategory(category));
    // go to category detail page
    router.push(`/categories/${category.id}/detail`);
  };
  useEffect(() => {
    if (shop_delete_status == 'succeeded') {
      // set back to idle
      dispatch(setShopDeleteStatus('idle'));
      // and then go back to shops
      navigate('/shops');
    }
  }, [shop_delete_status]); // Include history as a dependency

  const renderShopDetailSection = (
    // this is right section of shop detail
    <>
      <Grid item xs={12} sm={6} style={{}}>
        <Stack direction="column" spacing={3}>
          <Stack direction="row" spacing={3}>
            <Typography>{shop.name}</Typography>
          </Stack>
          <Stack direction="row" spacing={3}>
            <Typography>{shop.description}</Typography>
          </Stack>
          <Stack direction="row" spacing={3}>
            <Typography>{shop.address}</Typography>
          </Stack>
          <Stack direction="row" spacing={3}>
            <Typography>{shop.contact}</Typography>
          </Stack>
          <Stack direction="row" spacing={3}>
            <Typography>{shop.wifi_name}</Typography>
          </Stack>
          <Stack direction="row" spacing={3}>
            <Typography>{shop.wifi_password}</Typography>
          </Stack>
          <Stack direction="row" spacing={3}>
            <Typography>{shop.url}</Typography>
          </Stack>
          <Button
            component={Link}
            to={`/shops/${id}/edit`}
            variant="contained"
            color="inherit"
            startIcon={<Iconify icon="eva:plus-fill" />}
          >
            Edit Shop
          </Button>
          <Button
            component={Link}
            to={`https://qrmenu.mmsoftware100.com/shop/${shop.url}`}
            target="_blank"
            variant="contained"
            color="inherit"
            startIcon={<Iconify icon="eva:plus-fill" />}
          >
            Preview Shop Menu
          </Button>
          {/* loading button for delete */}
          Shop Delete Status : {shop_delete_status}
          <Button
            onClick={() => setOpen(true)}
            variant="contained"
            color="inherit"
            startIcon={<Iconify icon="eva:plus-fill" />}
          >
            Delete Shop
          </Button>
        </Stack>
      </Grid>
    </>
  );
  return (
    <>
      <Container sx={{ mb: 3 }}>
        {/* image , side by side , list of text  */}
        <Grid container spacing={2}>
          <Grid item xs={12} sm={6} style={{}}>
            {/* stack two image */}
            <img src={shop.background_image_url} alt={shop.name} loading="lazy" />

            <br></br>
            <br></br>
            <QRCode value={`https://qrmenu.mmsoftware100.com/shop/${shop.url}`} size={256} />
          </Grid>
          {renderShopDetailSection}
        </Grid>
      </Container>

      <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
        <Divider style={{ backgroundColor: '#f2f2f2', width: '70%' }} />
      </div>

      <Container spacing={3} sx={{ mt: 3 }}>
        <Stack
          spacing={3}
          direction="row"
          alignItems="center"
          justifyContent="space-between"
          mb={5}
        >
          <Typography variant="h4">Categories</Typography>

          <Button
            component={Link}
            to="/categories/create"
            variant="contained"
            color="inherit"
            startIcon={<Iconify icon="eva:plus-fill" />}
          >
            New Category
          </Button>
        </Stack>

        <Grid container spacing={3} sx={{ justifyContent: 'flex-start' }}>
          {shop.categories.map((category) => (
            <Grid item xs={12} sm={6} md={4} key={category.id} sx={{ mb: 3 }}>
              <Container onClick={() => handleCategoryCardClick(category)} sx={{ p: 0, m: 0 }}>
                <Paper elevation={3} style={{ padding: '16px', cursor: 'pointer' }}>
                  <Typography variant="h6" sx={{ p: 0, m: 0 }}>
                    {category.name}
                  </Typography>
                  <Typography variant="body2">{category.description}</Typography>
                </Paper>
              </Container>
            </Grid>
          ))}
        </Grid>
      </Container>

      {/* delete confirmation dialog */}
      <Dialog
        open={open}
        // onClose={() => handleClose(id)}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Delete Confirmation!</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Are you sure to delete this shop info?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => handleClose(0)} autoFocus>
            Cancel
          </Button>
          <Button onClick={() => handleClose(id, access_token)} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>

      {/* delete error dialog */}
      <Dialog
        open={shop_delete_status === 'error'}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Error!</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">{error}</DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleErrorDialogClose} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}
